























import { Circle, Tabbar, TabbarItem } from "vant";
import { Component, Vue, Emit } from "vue-property-decorator";
@Component({
  components: {
    [Circle.name]: Circle,
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem
  }
})
export default class test extends Vue {
  @Emit()
  private emitTab(e: any) {
    // console.info(e)
    switch (e) {
      case 0:
        return "电影";
      case 1:
        return "影院";
      case 2:
        return "我的";
    }
  }
  active: number = 0;
}
